import React,{Component} from 'react'
import './gallery.scss';
import gallery_1 from './assets/flipboard-1.png'
import gallery_2 from './assets/flipboard-2.png'
import gallery_3 from './assets/flipboard-3.png'
import gallery_4 from './assets/flipboard-4.png'
import gallery_5 from './assets/flipboard-5.png'
import gallery_6 from './assets/flipboard-6.png'
import gallery_7 from './assets/flipboard-7.png'
import gallery_8 from './assets/flipboard-8.png'
import gallery_9 from './assets/flipboard-9.png'

const gallery = [gallery_1,gallery_2,gallery_3,gallery_4,gallery_5,gallery_6,gallery_7,gallery_8,gallery_9]
console.log(gallery)
class Gallery extends Component {
  constructor(props){
    super(props)

    this.changeImage = this.changeImage.bind(this)
  }
  componentDidMount(){
    // this.changeImage()
    let interval  = setInterval(this.flip,3000)
    // window.setTimeout(()=>{
    //   setInterval(this.changeImage,3000)
    // },1000)
    // console.log();
  }
  changeImage(){
    console.log('changing image')

  }
  flip(){
    window.setTimeout(this.changeImage,1000)
    var flip1 = document.getElementsByClassName('flipper')[0]
    let front_1 = document.getElementsByClassName('front')[0];
    let back_1 = document.getElementsByClassName('back')[0];
    var t = flip1.style.transform
    if (t =="rotateY(0deg)"){
      back_1.firstElementChild.src = gallery[Math.floor(Math.random()*gallery.length)]
      flip1.style.transform = "rotateY(180deg)"
    }
    else {
      front_1.firstElementChild.src = gallery[Math.floor(Math.random()*gallery.length)]
      flip1.style.transform = "rotateY(0deg)"
    }
    // console.log(flip1)
  }
  render(){
    return (
      <div className = "gallery">
      <div className="flip-container">
    	<div className="flipper">
    		<div className="front">
    			<img src = {gallery_1}/>
    		</div>
    		<div className="back">
    			<img src = {gallery_2}/>
    		</div>
    	</div>
    </div>
  </div>
    )
  }
}


export default Gallery;


